import { forwardRef, useEffect, useImperativeHandle, useState } from 'react';
import { Col } from "reactstrap";
import { Dropdown, Checkbox } from "semantic-ui-react";

import { EctlCommon } from "../Common/EctlCommon.js";

let eqlist = [];
let updateshipjson;
let bisGearTable = {};

const ShipSelector = forwardRef((props, ref) => {
  const [theme, setTheme] = useState();
  const [selectorid, setSelectorid] = useState([]);
  const [shipFeedbackJson, setShipFeedbackJson] = useState({});
  const [shipList, setShipList] = useState([]);

  const [shipValue, setShipValue] = useState(0);
  const [shipHasKai, setShipHasKai] = useState(false);
  const [shipKai, setShipKai] = useState(false);

  const [equipment1List, setEquipment1List] = useState([]);
  const [equipment2List, setEquipment2List] = useState([]);
  const [equipment3List, setEquipment3List] = useState([]);
  const [equipment4List, setEquipment4List] = useState([]);
  const [equipment5List, setEquipment5List] = useState([]);

  const [equipment1, setEquipment1] = useState(0);
  const [equipment2, setEquipment2] = useState(0);
  const [equipment3, setEquipment3] = useState(0);
  const [equipment4, setEquipment4] = useState(0);
  const [equipment5, setEquipment5] = useState(0);

  useEffect(() => {
    setTheme(props.theme);
    const tempShipList = props.shiplist;
    setShipList(tempShipList);
    eqlist = props.eqlist;
    updateshipjson = props.updateshipjson;
    bisGearTable = props.bisGearTable;
    setSelectorid(props.selectorid);
    if (props.updateJson !== undefined) {
      for (let i = 0; i < tempShipList.length; i++) {
        if (tempShipList[i].file === props.updateJson.ship) {
          props.updateJson.selectorid = props.selectorid;
          checkShipKai(i, props.updateJson);
          setShipEquipment(i, null, props.updateJson);
          setShipValue(i);
        }
      }
    }

    // eslint-disable-next-line
  }, []);

  useImperativeHandle(ref, () => ({
    clearShip: () => {
      setShipValue(0);
      setShipHasKai(0);
      setShipHasKai(0);
      setEquipment1List([]);
      setEquipment2List([]);
      setEquipment3List([]);
      setEquipment4List([]);
      setEquipment5List([]);
      setEquipment1(0);
      setEquipment2(0);
      setEquipment3(0);
      setEquipment4(0);
      setEquipment5(0);
    },
  }));

  const toggleKai = (isKai) => {
    let tempjson = updateShipFeedbackJson(isKai, "kai");
    updateshipjson(tempjson, selectorid);
    setShipEquipment(shipValue, isKai);
  };

  const setShipEquipment = (shipIndex, isKai = null, updateJson = null) => {
    if (shipIndex === 0) {
      return;
    }
    const ship_json = updateJson
      ? require("../../assets/json/ship/" + updateJson.ship + ".json")
      : require("../../assets/json/ship/" + shipList[shipIndex].file + ".json");
    const shipHullName = ship_json.Type;
    let shipHasKai = ship_json.Remodel === "t";
    isKai = isKai === null ? shipHasKai : isKai;
    const eq1 = ship_json.Eq1Type;
    const eq2 = ship_json.Eq2Type;
    const eq3 = ship_json.Eq3Type;
    const shipName = updateJson ? updateJson.ship : shipList[shipIndex].file;
    const template = bisGearTable.template[bisGearTable.mapping[shipName]] || {};
    const equippableArr1 = getEquipmentListFromEquippable(
      eq1, shipHullName, isKai, shipHasKai, template.slot_1);
    const equippableArr2 = getEquipmentListFromEquippable(
      eq2, shipHullName, isKai, shipHasKai, template.slot_2);
    const equippableArr3 = getEquipmentListFromEquippable(
      eq3, shipHullName, isKai, shipHasKai, template.slot_3);
    const equippableArr4 = getEquipmentListFromEquippable(
      "Auxiliaries", shipHullName, isKai, shipHasKai, template.slot_4);
    const equippableArr5 = getEquipmentListFromEquippable(
      "Auxiliaries", shipHullName, isKai, shipHasKai, template.slot_5);
    setEquipment1List(equippableArr1);
    setEquipment2List(equippableArr2);
    setEquipment3List(equippableArr3);
    setEquipment4List(equippableArr4);
    setEquipment5List(equippableArr5);
    selectShipBisEquippment(
      shipName,
      equippableArr1,
      equippableArr2,
      equippableArr3,
      equippableArr4,
      equippableArr5,
      isKai,
      updateJson
    );
  };

  const selectShipBisEquippment = (
    shipName,
    equippableArr1,
    equippableArr2,
    equippableArr3,
    equippableArr4,
    equippableArr5,
    shipHasKai,
    updateJson = null
  ) => {
    let tempFeedbackJson = { kai: shipHasKai };
    tempFeedbackJson.ship = shipName;
    tempFeedbackJson.eq1save = false;
    tempFeedbackJson.eq2save = false;
    tempFeedbackJson.eq3save = false;
    tempFeedbackJson.eq4save = false;
    tempFeedbackJson.eq5save = false;
    if (!bisGearTable.mapping[shipName] && updateJson === null) {
      tempFeedbackJson.eq1 = equippableArr1[0].file;
      tempFeedbackJson.eq2 = equippableArr2[0].file;
      tempFeedbackJson.eq3 = equippableArr3[0].file;
      tempFeedbackJson.eq4 = equippableArr4[0].file;
      tempFeedbackJson.eq5 = equippableArr5[0].file;
      setShipFeedbackJson(tempFeedbackJson);
      updateshipjson(tempFeedbackJson, selectorid);
      return;
    }
    const template = bisGearTable.template[bisGearTable.mapping[shipName]];

    let gear1 = !template ? "" : bisGearTable.slot[template.slot_1].flat()[0].gear;
    let gear2 = !template ? "" : bisGearTable.slot[template.slot_2].flat()[0].gear;
    let gear3 = !template ? "" : bisGearTable.slot[template.slot_3].flat()[0].gear;
    let gear4 = !template ? "" : bisGearTable.slot[template.slot_4].flat()[0].gear;
    let gear5 = !template ? "" : bisGearTable.slot[template.slot_5].flat()[0].gear;

    if (updateJson) {
      if (updateJson.eq1) {
        gear1 = updateJson.eq1;
        tempFeedbackJson.eq1save = true;
      }
      if (updateJson.eq2) {
        gear2 = updateJson.eq2;
        tempFeedbackJson.eq2save = true;
      }
      if (updateJson.eq3) {
        gear3 = updateJson.eq3;
        tempFeedbackJson.eq3save = true;
      }
      if (updateJson.eq4) {
        gear4 = updateJson.eq4;
        tempFeedbackJson.eq4save = true;
      }
      if (updateJson.eq5) {
        gear5 = updateJson.eq5;
        tempFeedbackJson.eq5save = true;
      }
    }

    const g1len = equippableArr1.length;
    const g2len = equippableArr2.length;
    const g3len = equippableArr3.length;
    const g4len = equippableArr4.length;
    const g5len = equippableArr5.length;
    let g1found = false;
    let g2found = false;
    let g3found = false;
    let g4found = false;
    let g5found = false;

    for (let i = 0; i < Math.max(g1len, g2len, g3len, g4len, g5len); i++) {
      if (i < g1len && !g1found) {
        if (equippableArr1[i].file === gear1) {
          g1found = true;
          tempFeedbackJson.eq1 = equippableArr1[i].file;
          setEquipment1(i);
        }
      }

      if (i < g2len && !g2found) {
        if (equippableArr2[i].file === gear2) {
          g2found = true;
          tempFeedbackJson.eq2 = equippableArr2[i].file;
          setEquipment2(i);
        }
      }

      if (i < g3len && !g3found) {
        if (equippableArr3[i].file === gear3) {
          g3found = true;
          tempFeedbackJson.eq3 = equippableArr3[i].file;
          setEquipment3(i);
        }
      }

      if (i < g4len && !g4found) {
        if (equippableArr4[i].file === gear4) {
          g4found = true;
          tempFeedbackJson.eq4 = equippableArr4[i].file;
          setEquipment4(i);
        }
      }

      if (i < g5len && !g5found) {
        if (equippableArr5[i].file === gear5) {
          g5found = true;
          tempFeedbackJson.eq5 = equippableArr5[i].file;
          setEquipment5(i);
        }
      }
    }

    if (g1found === false) {
      tempFeedbackJson.eq1 = equippableArr1[0].file;
      setEquipment1(0);
    }
    if (g2found === false) {
      tempFeedbackJson.eq2 = equippableArr2[0].file;
      setEquipment2(0);
    }
    if (g3found === false) {
      tempFeedbackJson.eq3 = equippableArr3[0].file;
      setEquipment3(0);
    }
    if (g4found === false) {
      tempFeedbackJson.eq4 = equippableArr4[0].file;
      setEquipment4(0);
    }
    if (g5found === false) {
      tempFeedbackJson.eq5 = equippableArr5[0].file;
      setEquipment5(0);
    }

    setShipFeedbackJson(tempFeedbackJson);
    updateshipjson(
      tempFeedbackJson,
      updateJson ? updateJson.selectorid : selectorid
    );
  };

  const getEquipmentListFromEquippable = (str, shipHullName, isKai, shipHasKai, slotTemplate) => {
    const matchArr = str.match(/\(.+on retrofit\)/g);
    if (matchArr) {
      if (isKai) {
        str = matchArr[0].replace("(", "").replace("on retrofit)", "");
      } else {
        str.replace(matchArr[0], "");
      }
    }
    // avoiding Arrays.prototype.concat here because it's varargs
    const eq_list = Object.keys(eqlist).map((k) => eqlist[k]).reduce((x, y) => x.concat(y));
    const recommended = slotTemplate ? bisGearTable.slot[slotTemplate].flat().map((x) => EctlCommon.shallowCloneObject(eq_list.find((item) => x.gear === item.file))) : [];
    let equipArr = !shipHasKai || isKai ? recommended : [];
    const choiceTable = str.split("/");
    for (let i = 0; i < choiceTable.length; i++) {
      if (choiceTable[i] === "Auxiliaries") {
        equipArr = equipArr.concat(filterAuxByShipType(shipHullName).map(EctlCommon.shallowCloneObject));
      } else {
        equipArr = equipArr.concat(eqlist[EctlCommon.canonicalizeEquipmentType(choiceTable[i])].map(EctlCommon.shallowCloneObject));
      }
    }

    equipArr = EctlCommon.deduplicateArray(equipArr, "file");

    for (let i = 0; i < equipArr.length; i++) {
      equipArr[i].value = i;
    }
    return equipArr;
  };

  const filterAuxByShipType = (shipHullName) => {
    const shipHullCode = EctlCommon.getHullCodeFromStr(shipHullName);
    let matched_aux_list = [];

    for (const type of ['Auxiliary', 'Depth Charge', 'Sonar', 'ASW Bomber', 'ASW Helicopter']){
      for (const aux of eqlist[type]) {
        const eq_json = require('../../assets/json/eq_json/' + aux.file + '.json');
        if (eq_json[shipHullCode] === "1" || checkUniversalAux(eq_json)) {
          matched_aux_list.push({
            key: eq_json.Type.toLowerCase() + "_" + matched_aux_list.length,
            value: matched_aux_list.length,
            text: eq_json.Name,
            type: eq_json.Type,
            file: aux.file,
            image: {
              avatar: false,
              src: require('../../assets/img/eq_images/' + eq_json.index + '.png'),
            },
          });
        }
      }
    }
    return matched_aux_list;
  };

  const checkShipKai = (shipIndex, updateJson = null) => {
    const ship_json = updateJson
      ? require("../../assets/json/ship/" + updateJson.ship + ".json")
      : require("../../assets/json/ship/" + shipList[shipIndex].file + ".json");
    if (ship_json.Remodel === "t") {
      setShipHasKai(true);

      if (updateJson) {
        setShipKai(updateJson.kai);
        return updateJson.kai;
      } else {
        setShipKai(true);
        return true;
      }
    } else {
      setShipHasKai(false);
      if (updateJson) {
        setShipKai(updateJson.kai);
        return updateJson.kai;
      } else {
        setShipKai(false);
        return false;
      }
    }
  };

  const checkUniversalAux = (eq_json) => {
   if (EctlCommon.AllHullCodes.some((v) => eq_json.hasOwnProperty(v))){
    return false;
   } else {
    return true;
   }
  }

  const updateShipFeedbackJson = (val, prop, externalJson = null) => {
    let tempJson = externalJson ? { ...externalJson } : { ...shipFeedbackJson };
    tempJson[prop] = val;
    setShipFeedbackJson(tempJson);
    return tempJson;
  };

  const shipSearch = (options, query) => {
    const normalizedQuery = query.normalize("NFD").replace(/\p{Diacritic}/gu, "").toLowerCase()
    return options.filter((option) => {
      const shipName = option.text.normalize("NFD").replace(/\p{Diacritic}/gu, "").toLowerCase()

      return shipName.includes(normalizedQuery);
    });
  }

  return shipList.length === 0 ? (
    ""
  ) : (
    <Col md="4">
      <h5>{props.shiprole}</h5>
      <Dropdown
        className={(theme ? "semanticDropdownDark" : "") + " ectl-secondary ectl-text"}
        fluid
        selection
        labeled
        search={shipSearch}
        closeOnChange
        options={shipList}
        value={shipValue}
        onChange={(event, data) => {
          setShipValue(data.value);
          checkShipKai(data.value);
          setShipEquipment(data.value);
          if (data.value === 0) {
            setEquipment1List([]);
            setEquipment2List([]);
            setEquipment3List([]);
            setEquipment4List([]);
            setEquipment5List([]);
            setShipHasKai(false);
            updateshipjson(null, selectorid);
          }
        }}
      />
      {shipHasKai ? (
        <>
          <Checkbox
            label={
              <label>
                <span style={theme ? { color: "#FFFFFF" } : {}}>Kai</span>
              </label>
            }
            checked={shipKai}
            onChange={(event, data) => {
              setShipKai(data.checked);
              toggleKai(data.checked === true);
            }}
            toggle
          />
        </>
      ) : (
        ""
      )}
      {equipment1List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          className={(theme ? "semanticDropdownDark" : "") + " ectl-secondary ectl-text"}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment1List}
          value={equipment1}
          onChange={(event, data) => {
            setEquipment1(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq1"
            );
            tempjson = updateShipFeedbackJson(true, "eq1save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
      {equipment2List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          className={(theme ? "semanticDropdownDark" : "") + " ectl-secondary ectl-text"}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment2List}
          value={equipment2}
          onChange={(event, data) => {
            setEquipment2(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq2"
            );
            tempjson = updateShipFeedbackJson(true, "eq2save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
      {equipment3List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          className={(theme ? "semanticDropdownDark" : "") + " ectl-secondary ectl-text"}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment3List}
          value={equipment3}
          onChange={(event, data) => {
            setEquipment3(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq3"
            );
            tempjson = updateShipFeedbackJson(true, "eq3save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
      {equipment4List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          className={(theme ? "semanticDropdownDark" : "") + " ectl-secondary ectl-text"}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment4List}
          value={equipment4}
          onChange={(event, data) => {
            setEquipment4(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq4"
            );
            tempjson = updateShipFeedbackJson(true, "eq4save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
      {equipment5List.length === 0 ? (
        ""
      ) : (
        <Dropdown
          className={(theme ? "semanticDropdownDark" : "") + " ectl-secondary ectl-text"}
          fluid
          selection
          labeled
          search
          closeOnChange
          options={equipment5List}
          value={equipment5}
          onChange={(event, data) => {
            setEquipment5(data.value);
            let tempjson = updateShipFeedbackJson(
              data.options[data.value].file,
              "eq5"
            );
            tempjson = updateShipFeedbackJson(true, "eq5save", tempjson);
            updateshipjson(tempjson, selectorid);
          }}
        />
      )}
    </Col>
  );
});

export default ShipSelector;
